body {
	font-family: Arial, Helvetica, sans-serif;
	overflow-x: hidden;
}

// Start Resets

a, a:hover, a:focus {
	text-decoration: none;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	background: none;

	&:focus {
		outline: none;
	}
}

// End Resets